body {

    @apply dark:bg-gray-800 text-gray-700;

}

h1,
h2,
h3,
h4,
h5 {

    @apply font-bold;

}

h1 {

    @apply text-5xl;

}

h2 {

    @apply text-4xl;

}

h3 {

    @apply text-3xl;

}

h4 {

    @apply text-2xl;

}

p {

    @apply text-lg text-gray-500 dark:text-gray-300;
    
}